.preview {
    background: url("../img/bg-preview.jpg") center no-repeat;
    background-size: cover;
    padding: 100px 0 110px;

    @media screen and (max-width: 1365px) {
        padding: 80px 0 90px;
    }

    @media screen and (max-width: 1023px) {
        padding: 70px 0 80px;
    }

    @media screen and (max-width: 767px) {
        padding: 50px 0;
    }

    @media screen and (max-width: 579px) {
        padding: 40px 0 50px;
    }

    &__wrap {
        display: flex;
        align-items: center;
        justify-content: center;
        padding-left: 185px;

        @media screen and (max-width: 1365px) {
            padding-left: 85px;
        }

        @media screen and (max-width: 1023px) {
            padding-left: 60px;
        }

        @media screen and (max-width: 767px) {
            padding-left: 0;
        }

        @media screen and (max-width: 579px) {
            flex-flow: column nowrap;
        }
    }

    &__form {
        background: $white;
        padding: 32px 40px 34px 30px;
        border-radius: 20px;
        width: 300px;
        margin-right: 95px;
        box-shadow: 8px 8px 10px 0 rgba(0,0,0,.15);

        @media screen and (max-width: 1365px) {
            margin-right: 85px;
        }

        @media screen and (max-width: 1023px) {
            margin-right: 45px;
            width: 250px;
            padding: 25px;
            flex: 0 0 auto;
        }

        @media screen and (max-width: 767px) {
            padding: 20px 18px;
            width: 210px;
            margin-right: 35px;
        }

        @media screen and (max-width: 579px) {
            order: 2;
            width: 240px;
            margin-right: 0;
        }
    }

    &__title-wrap {
        padding-bottom: 15px;

        @media screen and (max-width: 1365px) {
            padding-bottom: 25px;
        }

        @media screen and (max-width: 579px) {
            order: 1;
        }
    }
}
